import JqxMenu from 'jqwidgets-scripts/jqwidgets-react-tsx/jqxmenu';
import React, { RefObject } from 'react';
import { Button, Icon, Menu, Sticky } from 'semantic-ui-react';
import { brack, makeprintformat } from '../../../utils/functionUtils';
import PermitPhotoUpload from '../../loggedInComponents/forms/mapPermitComponents/PermitPhotoUpload';
import { SingleFileUpload } from '../../loggedInComponents/forms/SingleFileUpload';
import { ActionComments, ApprovalStatus } from '../../shared/ApprovalStatusDisplay';
import GenericFloatingWindow from '../../shared/GenericFloatingWindow';
import { HistoryContent } from '../../shared/HistoryComponent';
import Printthis from '../../shared/Printthis';
import ShortcutHtml from '../../shared/ShortcutHtml';
import DynamicGenericFloatingWindow from './DynamicGenericFloatingWindow';
import { TStatuses } from './utils/dynamicFormUtils';

type TProps = {
	context: RefObject<HTMLDivElement>;
	isDisabled?: boolean;
	hasApprovePermission?: boolean;
	shortcut?: boolean;
	isRejected?: boolean;
	statuses: TStatuses[];
	onBeforeGetContent?: { [key: string]: string[] };
	content: HTMLDivElement;
	setPrintContent: (val: boolean) => void;
	approvalRef?: any;
	historyList: [];
	comments: [];
};
export const DynamicActionBar = ({
	onBeforeGetContent,
	context,
	isDisabled = false,
	hasApprovePermission = true,
	shortcut = false,
	statuses,
	isRejected,
	setPrintContent,
	content,
	historyList = [],
	comments = [],
	approvalRef,
}: TProps) => {
	let dupContent: Node;
	let printContent = onBeforeGetContent ? (
		<Printthis
			content={() => dupContent}
			onBeforeGetContent={() => {
				setPrintContent(true);
				dupContent = content.cloneNode(true);
				brack(dupContent);
				Object.values(onBeforeGetContent).forEach((each) => {
					//@ts-ignore
					return makeprintformat(dupContent, ...each);
				});
				if (approvalRef) {
					//@ts-ignore
					dupContent.append(approvalRef);
				}
			}}
			onBeforePrint={() => {
				setPrintContent(false);
			}}
			ui={<Button className="primary-btn" compact size="tiny" icon="print" content="Print" />}
		/>
	) : (
		<Printthis
			content={() => content}
			onBeforeGetContent={() => {
				setPrintContent(true);
			}}
			onBeforePrint={() => {
				setPrintContent(false);
			}}
			ui={<Button className="primary-btn" compact size="tiny" icon="print" content="Print" />}
		/>
	);

	return (
		<div>
			<Sticky context={context} offset={43} style={{ marginTop: '-26px', marginBottom: '20px', marginLeft: '-20px', marginRight: '-20px' }}>
				<JqxMenu>
					<ul>
						{hasApprovePermission && (
							<>
								<li style={{ color: '#099e2c', pointerEvents: isDisabled ? 'none' : 'auto', opacity: isDisabled ? 0.3 : 1 }}>
									<Button
										content="Approve"
										size="tiny"
										compact
										className="success-button"
										icon="thumbs up"
										onClick={
											() => {}
											// //@ts-ignore
											// if (isNoticePayment) this.approveRef.handleNoticeApproval(true);
											// //@ts-ignore
											// else if (isMapPermit) this.approveRef.handleMapPermitApproval(true);
											// else {
											// 	fileCategories &&
											// 	fileCategories.some(
											// 		//@ts-ignore
											// 		(category) => category.viewUrl && category.viewUrl.trim() === formUrl
											// 	)
											// 		? // @ts-ignore
											// 		  //@ts-ignore
											// 		  this.approveRef.handleApprovalModalConfirmOpen()
											// 		: //@ts-ignore
											// 		  //@ts-ignore
											// 		  this.approveRef.handleApprovalModalOpen();
											// }
										}
									/>{' '}
									{shortcut && <ShortcutHtml content="[alt + a]" />}
								</li>
								<li style={{ color: '#099e2c', pointerEvents: isDisabled ? 'none' : 'auto', opacity: isDisabled ? 0.3 : 1 }}>
									<Button
										content="Reject"
										size="tiny"
										compact
										className="danger-button"
										icon="thumbs down"
										onClick={
											() => {}
											// //@ts-ignore
											// if (isNoticePayment) this.approveRef.handleNoticeApproval(true);
											// //@ts-ignore
											// else if (isMapPermit) this.approveRef.handleMapPermitApproval(true);
											// else {
											// 	fileCategories &&
											// 	fileCategories.some(
											// 		//@ts-ignore
											// 		(category) => category.viewUrl && category.viewUrl.trim() === formUrl
											// 	)
											// 		? // @ts-ignore
											// 		  //@ts-ignore
											// 		  this.approveRef.handleApprovalModalConfirmOpen()
											// 		: //@ts-ignore
											// 		  //@ts-ignore
											// 		  this.approveRef.handleApprovalModalOpen();
											// }
										}
									/>
									{shortcut && <ShortcutHtml content="[alt + r]" />}
								</li>
							</>
						)}
						<li id="menuItemFileUpload" className="jqx-item jqx-menu-item-top jqx-rc-all" role="menuitem" style={{ float: 'left' }}>
							{/* //@ts-ignore */}

							<DynamicGenericFloatingWindow
								label="Upload File"
								icon="file"
								// setFileUploadRef={setFileUploadRef}
								button={true}
								//@ts-ignore
								// allupload={(x) => this.setState({ showAllCategoryUpload: x })}
							>
								<>
									Under Construction
									{/* {isMapPermit && <PermitPhotoUpload />}
									<SingleFileUpload url={formUrl} fileCategories={fileCategories} prevData={prevData} files={files} /> */}
								</>
							</DynamicGenericFloatingWindow>
						</li>{' '}
						<li id="menuItemFileUpload" className="jqx-item jqx-menu-item-top jqx-rc-all" role="menuitem" style={{ float: 'left' }}>
							{/* //@ts-ignore */}

							<DynamicGenericFloatingWindow
								label="View File"
								icon="file code"
								// setFileUploadRef={setFileUploadRef}
								button={true}
								//@ts-ignore
								// allupload={(x) => this.setState({ showAllCategoryUpload: x })}
							>
								<>
									Under Construction
									{/* {isMapPermit && <PermitPhotoUpload />}
									<SingleFileUpload url={formUrl} fileCategories={fileCategories} prevData={prevData} files={files} /> */}
								</>
							</DynamicGenericFloatingWindow>
						</li>
						<li>{printContent}</li>
						<li>
							<DynamicGenericFloatingWindow label="History" icon="history">
								<HistoryContent historyList={historyList} />
							</DynamicGenericFloatingWindow>
						</li>
						<li>
							<DynamicGenericFloatingWindow label="Comments" icon="comments" isRejected={isRejected}>
								<ActionComments comments={comments} />
							</DynamicGenericFloatingWindow>
						</li>{' '}
						<li>
							<ApprovalStatus
								dynamic={true}
								isRejected={isRejected}
								statuses={statuses}
								formUrl={'/users/forms/allowance-paper-view'}
							/>
						</li>
					</ul>
				</JqxMenu>
			</Sticky>
		</div>
	);
};
