type TApiObject = { api: string; objName: string };
type TReduxState = {
	root: {
		admin: {
			formNameMaster: IFormNameMaster[];
		};
		formData: {
			permitData: {};
			userData: {};
			comment: [];
			history: [];
			success: {};
			printcontent: any;
			fileCategories: any;
			files: any;
		};
		ui: {
			loading: boolean;
			errors: {};
		};
		general: {
			printcontent: any;
		};
		dynamic: any;
	};
};
type TProps = {
	// parentProps: { location: { pathname: string } };
	api: TApiObject[];
	// checkPreviousFiles?: boolean;
	// fetchFiles?: boolean;
	// localData?: {};
	// history: { push: (url: string) => void };
	// // Actions
	// getFormContainerData: (api: TApiObject[], prepareData: (data: any) => {}, formUtility: FormUtility, localData: {}, fetchFile: boolean) => void;
	// Redux state
	permitData: {};
	userData: {};
	formData: {};
	comment: [];
	formHistory: [];
	adminData: {
		formNameMaster: IFormNameMaster[];
	};
	loading: boolean;
	errors: {};
	success: {};
	printcontent: any;
	fileCategories: any;
	files: any;
	//Actions
	getPermitAndUserData: any;
	getDynamicFormContainerData: any;
	getFormContainerData: any;
	getAdminDataByUrl: any;
	postFormDataByUrl: any;
	postFormDataWithFileByUrl: any;
	setPrint: (val: boolean) => void;
	//Redux State
	dynamic: { cFormName: IFormNameMaster };
	// From Render Props Section
	parentProps: TDynamicFormProps;
	onBeforeGetContent?: { [key: string]: string[] };
	render: ({}: any) => Element;
};

type TState = {
	allLoaded: boolean;
	cFormMaster: IFormNameMaster | null;
	formUtility: DynamicFormUtility | null;
	applicationNo: string | null;
	formId: string | null;
	isFormSetupValid: boolean;
};

class DynamicFormContaner extends React.Component<TProps, TState> {
	formRef: RefObject<HTMLDivElement> = React.createRef();
	constructor(props: TProps) {
		super(props);
		this.state = {
			allLoaded: false,
			isFormSetupValid: false,
			applicationNo: null,
			formId: null,
			cFormMaster: null,
			formUtility: null,
		};
	}

	componentDidMount() {
		const {
			parentProps: {
				match: {
					params: { formId },
				},
			},
			api,
		} = this.props;
		this.props.getAdminDataByUrl(api);
		this.props.getDynamicFormContainerData(api, null, false, formId);
	}

	componentDidUpdate(prevProps: TProps) {
		const {
			adminData,
			dynamic,
			parentProps: {
				match: {
					params: { applicationNo, formId },
				},
			},
			api,
		} = this.props;

		if (formId !== prevProps.parentProps.match.params.formId) {
			this.props.getAdminDataByUrl(api);
		}

		if (dynamic !== prevProps.dynamic) {
			const { cFormName } = dynamic;
			if (cFormName && cFormName.formLayout && cFormName.formPermissions && cFormName.formFieldsList) {
				let formUtility;
				if (cFormName) {
					formUtility = new DynamicFormUtility(cFormName);
				}

				this.setState({
					isFormSetupValid: true,
					cFormMaster: cFormName || null,
					formUtility: formUtility || null,
				});
			} else {
				this.setState({ isFormSetupValid: false });
			}
		}
		if (this.props.adminData !== prevProps.adminData) {
			const dataNames = this.props.api;
			let hasLoaded: boolean[] = [];
			let data: any = {};

			dataNames.forEach((obj, index) => {
				//@ts-ignore
				if (!isEmpty(this.props.adminData[obj.objName]) || Array.isArray(this.props.adminData[obj.objName])) {
					hasLoaded[index] = true;
					Object.assign(data, {
						//@ts-ignore
						[obj.objName]: this.props.adminData[obj.objName],
					});
				} else {
					hasLoaded[index] = false;
				}
			});

			this.setState({
				allLoaded: hasLoaded.reduce((loaded, next) => loaded && next, true),
			});
		}

		if (this.props.success !== prevProps.success) {
			// this.props.getAfterUpdateAdminData(this.props.updateApi);
		}
	}

	//print status globally
	setPrintContent = (value: boolean) => {
		this.props.setPrint(value);
	};

	render() {
		const { isFormSetupValid, cFormMaster, formUtility } = this.state;
		const { loading, errors, formHistory, comment } = this.props;

		const { prefixValues: statuses, isRejected } = formUtility ? formUtility.getStatusNameValues({}) : { prefixValues: [], isRejected: false };
		if (!isFormSetupValid) {
			if (loading) return <FallbackComponent errors={errors} loading={loading} />;

			return (
				<div>
					<CompactError message={dynamicFormErrors.formSetupError} />
				</div>
			);
		}
		if (this.state.allLoaded) {
			return (
				<div>
					<DynamicActionBar
						content={this.formRef.current!}
						setPrintContent={this.setPrintContent}
						onBeforeGetContent={this.props.onBeforeGetContent}
						context={this.formRef}
						statuses={statuses}
						isRejected={isRejected}
						historyList={formHistory}
						comments={comment}
					/>
					<div ref={this.formRef}>{this.props.render({ ...this.props, cFormMaster })}</div>
				</div>
			);
		} else {
			return <FallbackComponent errors={this.props.errors} loading={this.props.loading} />;
		}
	}
}

const mapDispatchToProps = {
	getPermitAndUserData,
	getFormContainerData,
	getDynamicFormContainerData,
	postFormDataByUrl,
	getAdminDataByUrl,
	postFormDataWithFileByUrl,
	setPrint: (res: boolean) => setPrint(res),
};

const mapStateToProps = (state: TReduxState) => {
	return {
		permitData: state.root.formData.permitData,
		userData: state.root.formData.userData,
		formData: state.root.formData,
		loading: state.root.ui.loading,
		errors: state.root.ui.errors,
		success: state.root.formData.success,
		printcontent: state.root.general.printcontent,
		fileCategories: state.root.formData.fileCategories,
		files: state.root.formData.files,
		adminData: state.root.admin,
		dynamic: state.root.dynamic,
		comment: state.root.dynamic.comment,
		formHistory: state.root.dynamic.history,
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(DynamicFormContaner);
